}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Chakra Petch";
  font-display: swap;
  src: url("../fonts/ChakraPetch-Regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 500;
