*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Chakra Petch", "Arial", sans-serif;
  color: #ffffff;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #ffffff;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

:root {
  --header-height: 114px;
  --pricing-inner-padding-bottom: 46px;
}
@media (max-width: 1023px) {
  :root {
    --pricing-inner-padding-bottom: 89px;
  }
}
@media (max-width: 767px) {
  :root {
    --header-height: 116px;
  }
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  padding-top: var(--header-height);
  background-color: #18181b;
  overflow: hidden;
}
.wrapper--sitemap {
  background-color: #ffffff;
}
.wrapper__bg-wave {
  position: absolute;
  width: 736px;
  height: 353px;
}
@media (max-width: 767px) {
  .wrapper__bg-wave {
    width: 433px;
    height: 208px;
  }
}
.wrapper__bg-wave--upper {
  top: 0;
  right: 0;
}
.wrapper__bg-wave--lower {
  bottom: 0;
  left: 0;
  transform: rotate(180deg);
}
.wrapper__bg-grid {
  position: absolute;
  left: 50%;
  display: none;
  width: 2682px;
  height: 721px;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.wrapper--upper-grid .wrapper__bg-grid {
  top: 284px;
  transform: translateX(-2305px);
  display: block;
}
@media (max-width: 1023px) {
  .wrapper--upper-grid .wrapper__bg-grid {
    transform: translateX(-1720px);
  }
}

.wrapper--middle-grid .wrapper__bg-grid {
  top: 966px;
  transform: translateX(-2410px);
  display: block;
}
@media (max-width: 1023px) {
  .wrapper--middle-grid .wrapper__bg-grid {
    transform: translateX(-2080px);
  }
}

.rouble {
  /* stylelint-disable */
  font-weight: 400 !important;
  /* stylelint-enable */
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 24px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.header {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  min-height: 114px;
  padding: 10px 0;
  background-color: transparent;
  transition: min-height 0.6s ease, padding 0.6s ease;
}
@media (max-width: 767px) {
  .header {
    min-height: unset;
    padding: 42px 0;
  }
}
.header--blur {
  min-height: 90px;
  background-color: rgba(24, 24, 27, 0.7);
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px);
}
@media (max-width: 767px) {
  .header--blur {
    min-height: unset;
    padding: 20px 0;
  }
}
.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
.header__logo-box {
  order: 1;
  flex-shrink: 0;
  margin-bottom: 2px;
  margin-right: 65px;
  color: #ffffff;
}
@media (max-width: 767px) {
  .header__logo-box {
    display: flex;
    align-items: center;
    height: 32px;
    margin-bottom: 0;
    margin-right: auto;
  }
}
.header__nav {
  order: 2;
  max-width: 800px;
}
@media (max-width: 1023px) {
  .header__nav {
    max-width: 600px;
  }
}
@media (max-width: 767px) {
  .header__nav {
    order: 3;
    display: none;
    opacity: 0;
    animation: appearance 2s forwards;
  }
}
.header__toggle-box {
  order: 3;
  display: none;
}
@media (max-width: 767px) {
  .header__toggle-box {
    order: 2;
    display: inline-flex;
  }
}
.header__contact-btn-box {
  order: 4;
  max-width: 130px;
  margin-left: 20px;
}
@media (max-width: 767px) {
  .header__contact-btn-box {
    display: none;
    margin-left: 0;
    opacity: 0;
    animation: appearance 2s forwards;
  }
}
@media (max-width: 767px) {
  .header.is-open {
    overflow-y: scroll;
    width: 100vw;
    height: 100%;
    background-color: rgba(24, 24, 27, 0.7);
    backdrop-filter: blur(4px);
  }
}
@media (max-width: 767px) {
  .header.is-open .header__inner {
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
  }
}
@media (max-width: 767px) {
  .header.is-open .header__nav {
    display: block;
  }
}
@media (max-width: 767px) {
  .header.is-open .header__contact-btn-box {
    display: block;
  }
}

@keyframes appearance {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item {
  margin-top: 10px;
  margin-bottom: 10px;
  margin-right: 40px;
}
.main-nav__item:last-child {
  margin-right: 0;
}
.main-nav__link {
  padding-left: 12px;
  padding-right: 12px;
  font-size: 16px;
  font-style: normal;
  font-weight: 300;
  line-height: 28px;
  color: #ffffff;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .main-nav__link:not(.is-active):hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.main-nav__link:not(.is-active).focus-visible:focus {
  opacity: 0.8;
}
.main-nav__link.is-active {
  font-weight: 500;
}
.main-nav__toggle-box {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__toggle-box {
    display: inline-flex;
  }
}

@media (max-width: 767px) {
  .is-open .main-nav {
    margin-top: 20px;
    margin-bottom: 20px;
    width: calc(100vw - 32px);
  }
}
@media (max-width: 767px) {
  .is-open .main-nav__list {
    flex-direction: column;
  }
}

.footer {
  z-index: 1;
}
.footer__inner {
  display: grid;
  grid-template-columns: 130fr 412px 110fr;
  grid-template-rows: 121px 1fr;
  gap: 40px;
  height: 100%;
  margin-bottom: 33px;
  padding-top: 184px;
}
@media (max-width: 1279px) {
  .footer__inner {
    grid-template-columns: 124fr 375px 112fr;
  }
}
@media (max-width: 1023px) {
  .footer__inner {
    grid-template-columns: 2fr 3fr;
    grid-template-rows: 121px max-content 1fr;
  }
}
@media (max-width: 767px) {
  .footer__inner {
    grid-template-columns: 134px 1fr;
    grid-template-rows: max-content max-content 1fr;
    column-gap: 20px;
    row-gap: 36px;
    margin-bottom: 0;
    padding-top: 83px;
  }
}
@media (max-width: 429px) {
  .footer__inner {
    grid-template-rows: repeat(3, max-content) 1fr;
  }
}
@media (max-width: 767px) {
  .footer__logo-box {
    padding-top: 15px;
  }
}
@media (max-width: 429px) {
  .footer__logo-box {
    padding-top: 0;
    grid-column: 1/3;
  }
}
.footer__socials-box {
  grid-column: 1/2;
  grid-row: 2/3;
  margin-top: 8px;
}
@media (max-width: 767px) {
  .footer__socials-box {
    grid-column: 2/3;
    grid-row: 1/2;
    display: flex;
    justify-content: flex-end;
  }
}
@media (max-width: 429px) {
  .footer__socials-box {
    grid-column: 1/3;
    grid-row: 2/3;
    justify-content: flex-start;
    margin: 0;
  }
}
.footer__nav-wrapper {
  grid-column: 2/3;
  grid-row: 1/3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 56px;
}
@media (max-width: 1279px) {
  .footer__nav-wrapper {
    column-gap: 40px;
  }
}
@media (max-width: 767px) {
  .footer__nav-wrapper {
    grid-column: 1/3;
    grid-row: 2/3;
    grid-template-columns: 3fr 4fr;
    column-gap: 30px;
  }
}
@media (max-width: 429px) {
  .footer__nav-wrapper {
    grid-row: 3/4;
    padding-top: 20px;
  }
}
.footer__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.footer__item {
  margin-top: 16px;
}
.footer__link {
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.footer__link.focus-visible:focus {
  opacity: 0.8;
}
.footer__link, .footer__item address {
  font-weight: 300;
  line-height: 28px;
}
.footer__link-inner {
  display: flex;
}
.footer__link-icon-box {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
}
@media (max-width: 767px) {
  .footer__link-icon-box {
    margin-right: 11px;
  }
}
.footer__link-text-box p {
  margin: 0;
  font-style: normal;
}
.footer__contact-info-box {
  grid-column: 3/4;
  grid-row: 1/3;
}
@media (max-width: 1023px) {
  .footer__contact-info-box {
    grid-column: 2/3;
    grid-row: 3/4;
  }
}
@media (max-width: 767px) {
  .footer__contact-info-box {
    grid-column: 1/3;
    padding-top: 28px;
    padding-bottom: 48px;
  }
}
@media (max-width: 429px) {
  .footer__contact-info-box {
    grid-row: 4/5;
  }
}
.footer__legal-info {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100px;
  padding: 20px 0;
  font-weight: 300;
  line-height: 28px;
  border-top: 1px solid transparent;
  border-image: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
  border-image-slice: 1;
}
@media (max-width: 767px) {
  .footer__legal-info {
    min-height: 81px;
    padding-top: 28px;
  }
}
.footer__legal-info p {
  margin: 0;
}

.btn {
  display: inline-flex;
  padding: 18px 38px;
  background: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
  border: none;
  font-size: 14px;
  font-weight: 700;
  line-height: 14px;
  letter-spacing: 1.5px;
  color: #ffffff;
  text-transform: uppercase;
  user-select: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--transparent {
  padding: 17px 37px;
  background: unset;
  background-color: transparent;
  border: 1px solid transparent;
  border-image: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
  border-image-slice: 1;
}
.btn--small {
  padding: 11px 23px;
  font-size: 16px;
  font-weight: 300;
  line-height: 28px;
  letter-spacing: unset;
  text-transform: none;
}
.btn--burger {
  position: relative;
  display: flex;
  min-width: 32px;
  min-height: 32px;
  padding: 11px 10px;
  transition: border-image 0.3s ease, border-image-slice 0.3s ease;
}
.btn--burger::before, .btn--burger::after {
  content: "";
  position: absolute;
  top: 14px;
  left: -2px;
  display: block;
  width: 35px;
  height: 1px;
  background: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
  transition: opacity 0.3s ease;
}
.btn--burger::before {
  transform: rotate(45deg);
}
.btn--burger::after {
  transform: rotate(-45deg);
}
.btn--burger svg {
  transition: opacity 0.3s ease;
}
.btn--burger[aria-pressed=true] {
  border-image: unset;
