  line-height: 28px;
  color: #ffffff;
  background: transparent;
  border: none;
  outline: none;
}
.subscribe-form input::placeholder {
  font-size: 16px;
  font-weight: 400;
  line-height: 28px;
  color: #ffffff;
  transition: color 0.3s ease;
}
@media (max-width: 429px) {
  .subscribe-form input::placeholder {
    font-size: 12px;
  }
}
.subscribe-form input:focus::placeholder {
  color: transparent;
}
.subscribe-form__email-icon {
  position: absolute;
  top: 50%;
  left: 10px;
  z-index: 1;
  transform: translateY(-50%);
}
.subscribe-form__button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 49px;
  height: 49px;
  padding: 0;
  background: rgba(255, 255, 255, 0.21);
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .subscribe-form__button:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.subscribe-form__button.focus-visible:focus {
  opacity: 0.8;
}

.video {
  position: relative;
  width: 100%;
  padding-top: 43.5%;
}
@media (max-width: 767px) {
  .video {
    padding-top: 63.65%;
  }
}
.video__player {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 12px 12px 0 0;
  overflow: hidden;
}
@media (max-width: 767px) {
  .video__player {
    border-radius: 12px;
  }
}
.video video {
  width: 100%;
  height: 100%;
}
.video__info-box {
  position: absolute;
  top: 52px;
  left: 34px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-right: 35px;
  transition: opacity 0.3s ease;
}
.video__info-box--hidden {
  opacity: 0;
}
@media (max-width: 1023px) {
  .video__info-box {
    top: 22px;
    left: 15px;
    padding-right: 15px;
  }
}
.video__badge {
  margin-bottom: 12px;
}
.video__name {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.video__main-play-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 2;
  display: block;
  width: 100px;
  height: 100px;
  padding: 0;
  line-height: 0;
  font-size: 0;
  opacity: 0.3;
  background-color: transparent;
  border: none;
  outline: none;
  transition: opacity 0.3s ease, display 0.3s ease;
  transform: translate(-50%, -50%);
  cursor: pointer;
}
@media (max-width: 1023px) {
  .video__main-play-btn {
    width: 80px;
    height: 80px;
  }
}
@media (max-width: 767px) {
  .video__main-play-btn {
    width: 40px;
    height: 40px;
  }
}
@media (hover: hover) {
  .video__main-play-btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.video__main-play-btn.focus-visible:focus {
  opacity: 0.8;
}
.video__main-play-btn--hidden {
  opacity: 0;
  pointer-events: none;
}
@media (hover: hover) {
  .video__main-play-btn--hidden:hover:not(.focus-visible) {
    opacity: 0;
  }
}
.video__main-play-btn--hidden.focus-visible:focus {
  opacity: 0;
}
.video__main-play-btn svg {
  width: 100px;
  height: 100px;
}
@media (max-width: 1023px) {
  .video__main-play-btn svg {
    width: 80px;
    height: 80px;
  }
}
@media (max-width: 767px) {
  .video__main-play-btn svg {
    width: 40px;
    height: 40px;
  }
}
.video__control-bar {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: flex;
  width: 100%;
  padding: 33px;
}
@media (max-width: 1023px) {
  .video__control-bar {
    padding: 24px;
  }
}
@media (max-width: 767px) {
  .video__control-bar {
    padding: 20px 16px;
  }
}
.video__play-btn {
  position: relative;
  display: block;
  width: 12px;
  height: 14px;
  margin-right: 14px;
  padding: 0;
  line-height: 0;
  font-size: 0;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .video__play-btn {
    margin-right: 12px;
  }
}
.video__play-btn svg#icon-pause {
  display: none;
}
.video__play-btn.is-active svg#icon-play {
  display: none;
}
.video__play-btn.is-active svg#icon-pause {
  display: block;
}
.video__seek-bar {
  flex-grow: 1;
}

.personal-card {
  width: 290px;
  height: 386px;
  padding: 8px;
  border: 1px solid transparent;
  border-image: linear-gradient(90deg, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0.1) 100%);
  border-image-slice: 1;
  transition: border-image 0.3s ease;
}
@media (hover: hover) {
  .personal-card:hover:not(.focus-visible) {
    border-image: linear-gradient(90deg, #0cbaf1 0%, #e95ce9 100%);
    border-image-slice: 1;
  }
}
@media (max-width: 767px) {
  .personal-card {
    height: 389px;
  }
}
.personal-card__inner {
  position: relative;
  width: 100%;
  height: 100%;
  align-items: 0;
  overflow: hidden;
}
.personal-card__inner img, .personal-card__gradient, .personal-card__info-box {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.personal-card__gradient {
  z-index: 1;
  background: linear-gradient(180deg, rgba(50, 70, 188, 0) 0%, #192883 100%);
}
.personal-card__info-box {
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  padding: 9px;
}
.personal-card__info-box p {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
  line-height: 28px;
}
.personal-card__info-box span {
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  color: #b0b0b2;
}

